@font-face {
  font-family: "Trebuchet MS";
  src: local("montserrat-alternates-regular"),
    url("../fonts/trebuchetms.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}


@font-face {
  font-family: "Trebuchet MS";
  src: local("trebuchetms-bold"),
    url("../fonts/trebuchetms-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Exo 2';
  src: local('exo2-thin'),
    url('../fonts/exo2-thin.woff2') format('woff2'),
    url('../fonts/exo2-thin.woff') format('woff');
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
