@font-face {
  font-family: "Trebuchet MS";
  src: local("montserrat-alternates-regular"), url("../fonts/trebuchetms.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Trebuchet MS";
  src: local("trebuchetms-bold"), url("../fonts/trebuchetms-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Exo 2";
  src: local("exo2-thin"), url("../fonts/exo2-thin.woff2") format("woff2"), url("../fonts/exo2-thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.page {
  scroll-behavior: smooth;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-image: url("../img/bcg-image.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: auto;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}
.main h1 {
  font-family: "Exo 2", "Arial", sans-serif;
  font-size: 36px;
  line-height: 42px;
  font-weight: 100;
  font-style: normal;
  text-align: center;
  margin: 0;
  color: #ffffff;
  padding-bottom: 25px;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 577px;
  padding: 37px 20px 67px;
}

.products__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 80px;
  grid-template-columns: repeat(3, 1fr);
}

.products__item {
  width: 320px;
  min-height: 509px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.products__item--selected .products__amount {
  background-color: #d91667;
}
.products__item--selected::after {
  background-color: #d91667;
}
.products__item--selected .products__example {
  border-color: #d91667;
}
.products__item--selected .products__example::before {
  background-color: #d91667;
}
.products__item:hover .products__amount, .products__item:focus-visible .products__amount {
  background-color: #2ea8e6;
}
.products__item:hover::after, .products__item:focus-visible::after {
  background-color: #2ea8e6;
}
.products__item:hover .products__example, .products__item:focus-visible .products__example {
  border-color: #2ea8e6;
}
.products__item:hover .products__example::before, .products__item:focus-visible .products__example::before {
  background-color: #2ea8e6;
}

.products__item--selected:hover .products__amount, .products__item--selected:focus-visible .products__amount {
  background-color: #e52e7a;
}
.products__item--selected:hover::after, .products__item--selected:focus-visible::after {
  background-color: #e52e7a;
}
.products__item--selected:hover .products__example, .products__item--selected:focus-visible .products__example {
  border-color: #e52e7a;
}
.products__item--selected:hover .products__example::before, .products__item--selected:focus-visible .products__example::before {
  background-color: #e52e7a;
}

.products__example {
  padding: 17px 59px 0 48px;
  transition: 0.3s ease-in-out;
  border: 4px solid #1698d9;
  border-radius: 0 12px 12px 12px;
  background-color: #ffffff;
  min-height: 480px;
  width: 100%;
  clip-path: polygon(14% 0%, 100% 0%, 100% 0%, 100% 100%, 100% 100%, 0% 100%, 0% 100%, 0% 9%);
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.products__example::before {
  transition: 0.3s ease-in-out;
  position: absolute;
  content: "";
  width: 105px;
  height: 4px;
  background-color: #1698d9;
  transform: rotate(135deg);
  left: -33px;
  z-index: 5;
}
.products__example::after {
  position: absolute;
  content: url("../img/card-cat.png");
  left: -28px;
  bottom: -96px;
}

.products__item--disabled .products__disabled {
  background-color: rgba(255, 255, 255, 0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 4;
}
.products__item--disabled .products__item-descr {
  color: #ffff66;
}
.products__item--disabled .products__example {
  border-color: #b3b3b3;
  cursor: not-allowed;
}
.products__item--disabled .products__example::before {
  background-color: #b3b3b3;
}
.products__item--disabled .products__amount {
  background-color: #b3b3b3;
}
.products__item--disabled:hover .products__amount, .products__item--disabled:focus-visible .products__amount {
  background-color: #b3b3b3;
}
.products__item--disabled:hover::after, .products__item--disabled:focus-visible::after {
  background-color: #b3b3b3;
}
.products__item--disabled:hover .products__example, .products__item--disabled:focus-visible .products__example {
  border-color: #b3b3b3;
}
.products__item--disabled:hover .products__example::before, .products__item--disabled:focus-visible .products__example::before {
  background-color: #b3b3b3;
}

.products__item-descr {
  font-family: "Trebuchet MS", "Verdana", sans-serif;
  font-size: 13px;
  line-height: 15px;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  padding-top: 14px;
  color: #ffffff;
  transition: 0.3s ease-in-out;
}
.products__item-descr span {
  color: #1698d9;
}
.products__item-descr a {
  font-weight: 700;
  color: #1698d9;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  border-bottom: dashed 1px #1698d9;
}

.products__slogan,
.products__name,
.products__type,
.products__quantity {
  margin: 0;
}

.products__slogan,
.products__quantity {
  color: #666666;
}

.products__slogan {
  padding-bottom: 5px;
  font-family: "Trebuchet MS", "Verdana", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  font-style: normal;
}

.products__name,
.products__type {
  color: #000000;
  font-family: "Trebuchet MS", "Verdana", sans-serif;
  font-size: 48px;
  line-height: 56px;
  font-weight: 700;
  font-style: normal;
}

.products__type {
  font-size: 24px;
  line-height: 28px;
  padding-bottom: 15px;
}

.products__quantity {
  font-family: "Trebuchet MS", "Verdana", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  font-style: normal;
}

.products__amount {
  text-align: center;
  width: 80px;
  height: 80px;
  background-color: #1698d9;
  border-radius: 50%;
  transition: 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 5;
  right: 12px;
  bottom: 12px;
}
.products__amount p {
  font-family: "Trebuchet MS", "Verdana", sans-serif;
  font-size: 42px;
  line-height: 22px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  margin: 0;
}
.products__amount span {
  color: #ffffff;
  font-size: 19px;
  line-height: 0px;
}